  font-size: 70px;
  font-weight: 700;
  line-height: 0.9;
}

.discount__item--main a {
  font-size: 14px;
  border-bottom-color: #ffffff;
}
@media (max-width: 767px) {
  .discount__item--main a {
    align-self: center;
  }
}

.discount__item--main img {
  object-fit: cover;
  width: 100%;
}
@media (max-width: 1199px) {
  .discount__item--main img {
    aspect-ratio: 1/1.16;
    object-position: top center;
  }
}
@media (max-width: 767px) {
  .discount__item--main img {
    aspect-ratio: 1/1.03;
  }
}

